<template>
    <div class="market-class">
        <div class="list-h">
            <header class="header">
                <div class="container">
                    <span @click="btnGo" class="btnGo"></span>
                    <div @click="searchData" class="input-main fl"><input v-model="$store.state.search_value" type="text" disabled="disabled" placeholder="请输入您需要的产品!"></div>
                </div>
            </header>
        </div>
        <div class="content-wrap clearfix" ref="content">
            <ul class="nav-left fl">
                <li v-for="(item,index) in data" :key="item.id" :class="{active:lcassif.status[index]}" @click="tab(item,index)">{{item.class_name}}</li>
            </ul>
            <div class="content fr" ref="clss_content">
                <div class="tab-content-wrap">
                    <div class="item-one" v-for="(item,index) in condata" :key="item.id">
                        <div @click="link(item)">
                            <h2 class="title">{{item.class_name}}</h2>
                        </div>
                        <img v-lazy="URL+item.pic_url" v-if="item.pic_url" @click="tolink(item)">
                        <ul class="con clearfix" v-if="item.child">
                            <li class="fl" v-for="(con,index) in item.child" :key="con.id" @click="to(con)">
                                <img v-lazy="URL + con.pic_url">
                                <p class="text1-hidden">{{con.class_name}}</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="load" v-show="load" @touchmove.prevent>
            <mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner>
        </div>
        <!-- <div class="load-wrap" v-show="$store.state.class_load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div> -->
    </div>
</template>

<script>
    import qs from 'qs';
    export default {
        name: 'superClass',
        data() {
            return {
                data: '',
                lcassif: {
                    status: [true, false, false, false]
                },
                condata: '',
                status: '',
                load_wrap: true,
                load: false,
                dataIndex:''
            }
        },
        beforeRouteLeave(to, from, next) {
            let dataIndex = '';
            if(sessionStorage.getItem('classId')&&this.dataIndex === ''){
                let classInfo = JSON.parse(sessionStorage.getItem('classId'));
                dataIndex = classInfo.dataIndex;
            }
            if(this.dataIndex!==''){
                 dataIndex = this.dataIndex;
            }
            sessionStorage.setItem('classId',JSON.stringify({status:this.status,dataIndex:dataIndex}));
            this.$store.commit('changeRecruitScrollY', ''); //离开路由时把位置清空
            this.$store.state.listData = [];
            this.$store.state.listInfo = {};
            this.$store.state.changeHomeScrollY = 0;
            this.$store.state.groupScrollY = 0;
            this.$store.state.groupData = [];
            next();
        },
        mounted() {
            document.body.scrollTop = 0;
            this.$store.state.search_value = '';
            // const hdH = this.$refs.hd.$el.offsetHeight;
            // const clHeight = document.body.clientHeight;
            // this.$refs.content.style.height = `${clHeight - hdH}px`;
            
                this.axios({
                    url: API_URL + 'Home/class/navigation',
                    method: 'get'
                }).then((res) => {
                    this.data = res.data.data;
                    if(sessionStorage.getItem('classId')){
                        let classInfo = JSON.parse(sessionStorage.getItem('classId'));
                        for (var i = 0; i < this.lcassif.status.length; i++) {
                            this.$set(this.lcassif.status, i, false);
                        }
                        this.status = classInfo.status;
                        this.$set(this.lcassif.status, classInfo.dataIndex, true);
                    }else{
                        this.status = res.data.data[0].id;
                    }
                    this.succ();
                }).catch((err) => {
                    console.log(err);
                });         
        },
        methods: {
            searchData() {
                this.$router.push("search");
            },
            tab(item, index) {
                for (var i = 0; i < this.lcassif.status.length; i++) {
                    this.$set(this.lcassif.status, i, false);
                }
                this.$set(this.lcassif.status, index, true);
                this.status = this.data[index].id;
                this.dataIndex = index;
                this.load = true;
                this.succ(item);
            },
            succ(item) {
                this.axios({
                    url: API_URL + 'Home/class/category',
                    method: 'get',
                    params: {
                        'fid': this.status
                    }
                }).then((suc) => {
                    this.condata = suc.data.data;
                    this.load = false;
                    this.$refs.clss_content.scrollTop = 0;
                }).catch((err) => {
                    console.log(err);
                });
            },
            link(item) {
                if (!item.child.length == 0 || !item.child) {
                    return false;
                } else {
                    this.$router.push({
                        name: 'comList',
                        params: {
                            status: item.id
                        }
                    });
                }
    
            },
            btnGo() {
                this.$router.go(-1);
            },
            tolink(item) {
                if (item.pic_url) {
                    this.$router.push({
                        name: 'comList',
                        params: {
                            status: item.id
                        }
                    });
                }
    
            },
            to(con) {
                this.$router.push({
                    name: 'comList',
                    params: {
                        status: con.id
                    }
                });
            }
        }
    }
</script>

<style lang="less" scoped>
    .market-class {
        background: #fff;
        .list-h {
            height: .94rem;
        }
        .header {
            position: fixed;
            z-index: 9999;
            top: 0;
            left: 0;
            width: 100%;
            height: .62rem;
            background: #fff;
            padding: .16rem 0;
            text-align: center;
            color: #fff;
            font-size: .36rem;
            font-weight: bold;
            line-height: .62rem;
            .container {
                height: 100%;
                position: relative;
                top: 0;
                left: 0;
                .input-main {
                    width: 5.4rem;
                    height: 100%;
                    border-radius: 100px;
                    background: url(../../assets/nav_search.png) no-repeat .24rem center;
                    background-size: .35rem .35rem;
                    background-color: #f7f7f7;
                    line-height: 100%;
                    margin-left: 1rem;
                    input {
                        height: 100%;
                        width: 100%;
                        border: none;
                        background: none;
                        text-indent: .7rem;
                        font-size: .26rem;
                        color: #999;
                        outline: none;
                    }
                    input::-webkit-input-placeholder,
                    textarea::-webkit-input-placeholder {
                        color: #999;
                    }
                    input:-moz-placeholder,
                    textarea:-moz-placeholder {
                        color: #999;
                    }
                    input::-moz-placeholder,
                    textarea::-moz-placeholder {
                        color: #999;
                    }
                    input:-ms-input-placeholder,
                    textarea:-ms-input-placeholder {
                        color: #999;
                    }
                }
                .btnGo {
                    position: absolute;
                    transform: rotate(360deg);
                    -ms-transform: rotate(180deg);
                    /* IE 9 */
                    -moz-transform: rotate(180deg);
                    /* Firefox */
                    -webkit-transform: rotate(180deg);
                    /* Safari 和 Chrome */
                    -o-transform: rotate(180deg);
                    /* Opera */
                    left: .2rem;
                    top: 50%;
                    margin-top: -.23rem;
                    width: .46rem;
                    height: .46rem;
                    background: url(../../assets/btn-return005.png) no-repeat;
                    background-size: 100% 100%;
                }
            }
        }
        .mint-tab-item {
            display: inline-block;
            flex: none;
            .mint-tab-item-label {
                font-size: .26rem;
                line-height: .7rem;
            }
        }
        .content-wrap {
            position: fixed;
            top: .95rem;
            bottom: 1.1rem;
            background: #f2f2f2;
            .nav-left {
                height: 100%;
                width: 1.8rem;
                overflow-y: auto;
                background: #fff;
                li {
                    width: 100%;
                    height: 1rem;
                    line-height: 1rem;
                    text-align: center;
                    overflow: hidden;
                    font-size: .24rem;
                    background: #f2f2f2;
                    color: #000;
                    margin-bottom: .02rem;
                }
                li.active {
                    border-left: 2px solid #ff3c44;
                    box-sizing: border-box;
                    background: #fff;
                    color: #e26b6f;
                }
            }
            .content {
                overflow-y: auto;
                width: 5.7rem;
                height: 100%;
                background: #fff;
                .tab-content-wrap {
                    img {
                        width: 1.52rem;
                        height: 1.52rem;
                    }
                }
                .item-one {
                    .title {
                        height: .73rem;
                        line-height: .73rem;
                        font-size: 0.3rem;
                        color: #000;
                        text-indent: .5rem;
                    }
                    .con {
                        li {
                            width: 1.72rem;
                            height: 2.24rem;
                            text-align: center;
                            margin-left: .16rem;
                            background: #fff;
                            padding-top: .12rem;
                            box-sizing: border-box;
                            margin-bottom: .12rem;
                            img {
                                width: 1.52rem;
                                height: 1.52rem;
                            }
                            p {
                                font-size: .24rem;
                                color: #797979;
                                height: .64rem;
                                line-height: .64rem;
                                padding: 0 .2rem;
                            }
                        }
                    }
                }
                .item-tab {
                    margin-top: .1rem;
                    .mint-navbar {
                        border-bottom: 1px solid #f2f2f2;
                    }
                    .mint-tab-item {
                        padding: 0;
                        color: #666;
                        border-color: #f9781e;
                        margin: 0;
                        height: .7rem;
                        line-height: .7rem;
                        margin-left: .4rem;
                    }
                    .mint-tab-item.is-selected {
                        color: #f9781e;
                    }
                }
                .ification-wrap {
                    .title {
                        height: 1rem;
                        line-height: 1rem;
                        font-size: .26rem;
                        color: #333;
                        font-weight: bold;
                    }
                    .tab-content {
                        li {
                            margin-left: .3rem;
                            margin-right: .1rem;
                            text-align: center;
                            img {
                                width: 1.24rem;
                                height: 1.24rem;
                                border-radius: 100%;
                                box-sizing: border-box;
                            }
                            p {
                                font-size: .24rem;
                                color: #797979;
                                line-height: .7rem;
                                width: 1.24rem;
                                overflow: hidden;
                            }
                        }
                    }
                }
            }
        }
    }
</style>



